import control from "./control";

const VALUE_CHANGE = "model-value-mutation";
const VALUE_UPDATE_SYNC = "update:value";

/**
 * @class FormControl
 * @description 
 * @augments Control
 * @example
 * import formControl from "./formControl";
 *
 * export default {
 *    name: "xxxx",  name
 *    ctrlCaption: "xxx", // 
 *    mixins: [formControl],
 *    ...
 * }
 */
export default {
  model: {
    prop: "value",
    event: VALUE_CHANGE
  },
  /**
   * @memberof FormControl
   * @name Props 
   * @property {Boolean} [isFormControl = true] 
   * @property {String} [label = ""] 
   * @property {Boolean} [showLabel = true] label
   * @property {Boolean} [readOnly = false] 
   * @property {Boolean} [disabled = false] 
   * @property {Boolean} [allowPost = true] 
   * @property {Boolean} [required = false] 
   * @property {String|Number|Object|Array|Boolean} [value = null] {text:'',value:''}
   * @property {String} [dataCapion = ""] 
   * @property {String} [dataAudioUrl = ""] mp3
   * @property {String} [dataVideoUrl = ""] mp4
   * @property {Object} [metadata = null] 
   * @property {String} [readOnlyShowText = text] 
   * @property {String} [metadata_fullcode = ""] 
   * @property {Number} [metaDataLevel = 0] 01
   * @property {String} [validateRule = ""] 
   * @property {Array} [errors = []] 
   */
  props: {
    //
    isFormControl: {
      type: Boolean,
      default: true
    },
    //
    label: {
      type: String,
      default: ""
    },
    //label
    showLabel: {
      type: Boolean,
      default: true
    },
    //
    readOnly: {
      type: Boolean,
      default: false
    },
    //
    disabled: {
      type: Boolean,
      default: false
    },
    //
    allowPost: {
      type: Boolean,
      default: true
    },
    //null
    isPostNull: {
      type: Boolean,
      default: true
    },
    //
    required: {
      type: Boolean,
      default: false
    },
    //{text:'',value:''}
    value: {
      type: [String, Number, Object, Array, Boolean],
      default() {
        return null;
      }
    },
    //
    dataCapion: {
      type: String,
      default: ""
    },
    //mp3
    dataAudioUrl: {
      type: String,
      default: ""
    },
    //mp4
    dataVideoUrl: {
      type: String,
      default: ""
    },
    //
    metadata: {
      type: Object,
      default() {
        return null;
      }
    },
    //
    readOnlyShowText: {
      type: String,
      default: "text"
    },
    //
    metadata_fullcode: {
      type: String,
      default: ""
    },
    //
    simple: {
      type: Boolean,
      default: false
    },
    //01
    metaDataLevel: {
      type: Number,
      default: 0
    },
    //
    formatter: {
      type: String,
      default: ""
    },

    //validateRuleisValidate==================
    //
    validateRule: {
      type: String,
      default: ""
    },
    //
    isValidate: {
      type: Boolean,
      default: true
    },
    //validateRuleisValidate==================

    //
    colSpan: {
      type: Number,
      default: 24
    },
    labelWidth: {
      type: Number,
      default: 120
    },
    //,<span>
    textProxy: {
      type: Boolean,
      default: false
    },
    //
    textProxyIcon: {
      type: String,
      default: ""
    },
    //
    textProxyIconColor: {
      type: String,
      default: ""
    },
    // 
    immediatelyAudit: {
      type: Boolean,
      default: false
    },
    //name
    // formName: {
    //   type: String,
    //   default: ''
    // }
  },
  data() {
    return {
      _errors: []
    };
  },
  mixins: [control],
  computed: {
    validating() {
      return this?.$form?.validating
    },
    errors: {
      get() {
        if (this?.$form?.$errors) {
          let errors = _.filter(this?.$form?.$errors, (it) => it.target == this.$formName)
          return errors
        }
        else {
          return this.$data._errors;
        }

      },
      set(value) {
        if (this?.$form?.pushError) {
          this.$form.pushError(value);
        }
        else {
          return this.$data._errors.push(...value);
        }
      }
    },
    getCss() {
      return [
        ...this.$getClass(),
        this.errors.length > 0 ? "ds-error" : "",
        this.showLabel ? "ds-has-label" : "",
        this.required ? "ds-required" : "",
        this.readOnly ? "ds-readonly" : "",
        !this.showLabel && this.required ? "ds-nolabel-required" : '',
        this.disabled ? "ds-disabled" : "",
      ];
    },
    errorsMsg() {
      return _.map(this.errors, "message").join(",");
    },
    $labelWidth() {
      if (this.$page) {
        return this.$page.formItemLabelWidth || this.labelWidth || 120;
      }
      return this.labelWidth;
    },
    formName() {
      return this.$attrs['form-name'] || this.metadata_fullcode || ""
    },
    $formName() {
      let formName = this.formName;
      if (formName) {
        return this.formName + (this.$dataHash ? "-" + this.$dataHash : '');
      }
      return "";
    }
  },
  created() {
    if (!this.isDesign && this.$form) {
      this.$form.$addFiled(this);
    }
    if (!this.isDesign && this.$dynamicForm) {
      this.$dynamicForm.$addFiled(this);
    }
  },
  mounted() {
    if (this?.$el?.nodeType == 1) {
      this.$el.setAttribute("form-name", this.$formName);
    }
  },
  updated() {
    if (this?.$el?.nodeType == 1) {
      this.$el.setAttribute("form-name", this.$formName);
    }
  },
  beforeDestroy() {
    if (!this.isDesign && this.$form) {
      this.$form.$removeFiled(this);
    }
    if (!this.isDesign && this.$dynamicForm) {
      this.$dynamicForm.$removeFiled(this);
    }
  },
  /**
   * @memberof FormControl
   * @name Event 
   * @property {Function} valueMutation  [value-mutation]
   * @example
   * <components @value-mutation="CallbackFn" ></components>
   * //  valueMutation   - 
   */
  methods: {
    showHelper() {
      this.$openDialog({
        title: "",
        template: `<div v-html="params.value"></div>`,
        width: "30vw",
        height: "50vh",
        params: {
          value: this.dataCapion
        }
      });
    },
    getLabelWidth(isToRem) {
      return isToRem ? this.pxToRem(this.$labelWidth + "px") : this.$labelWidth + "px";
    },
    getLabelWidthStyle(isToRem) {
      if (!isToRem) {
        if (this.$labelWidth && this.showLabel) {
          return {
            width: this.$labelWidth + "px"
          };
        }
        return {};
      } else {
        if (this.$labelWidth && this.showLabel) {
          return {
            width: this.pxToRem(this.$labelWidth + "px")
          };
        }
        return {};
      }
    },
    getFormItemBlockMarginLeft() {
      if (this.$labelWidth && this.showLabel) {
        return {
          "margin-left": this.$labelWidth + "px"
        };
      }
      return {};
    },
    getShowContent() {
      if (dsf.type(this.value) == "object") {
        return this.value[this.readOnlyShowText];
      } else if (dsf.type(this.value) == "array") {
        return _.map(this.value, this.readOnlyShowText).join(",");
      } else {
        return this.value;
      }
    },
    $emitValueChange(v) {
      //v-model:value.sync
      let listeners = this.$listeners || {};
      //v-model
      if (listeners[VALUE_CHANGE]) {
        this.$emit(VALUE_CHANGE, v);
      }
      //value.sync
      else if (listeners[VALUE_UPDATE_SYNC]) {
        this.$emit(VALUE_UPDATE_SYNC, v);
      }
    },
    async emitFormValidate(v, i) {
      if (this.$form) {
        let localIndex = null;
        if (this.$list) {
          localIndex = this.local.$index;
        }
        return await this.$form.validateField(this.value, (dsf.isDef(i) ? i : localIndex), this);
      }
      return [];
    },
    emitValueChange(v) {
      this.$emitValueChange(v);
      this.$dispatch("value-change", v);
    }
  },
  watch: {
    allowPost: {
      handler(v, ov) {
        if (!this.isDesign && this.designId) {
          let code = this.metadata_fullcode;
          if (code) {
            if (this.$list && this.$list.isOneToMany && this.local?.$hash) {
              code = [this.local?.$hash, code].join("^");
            }
          }
          if (v) {
            this?.$vm?.removeNotAllowPostKey && this.$vm.removeNotAllowPostKey(code);
          } else {
            this?.$vm?.addNotAllowPostKey && this.$vm.addNotAllowPostKey(code);
          }
        }
      },
      immediate: true
    },
    isPostNull: {
      handler(v, ov) {
        if (!this.isDesign && this.designId) {
          let code = this.metadata_fullcode;
          if (code) {
            if (this.$list && this.$list.isOneToMany && this.local?.$hash) {
              code = [this.local?.$hash, code].join("^");
            }
          }
          if (v) {
            this?.$vm?.addPostNullKey && this.$vm.addPostNullKey(code);
          } else {
            this?.$vm?.removePostNullKey && this.$vm.removePostNullKey(code);
          }
        }
      },
      immediate: true
    }
  },
  design: {
    infoButton: true,
    layout: {
      exclude: ["value"]
    },
    metadata: {
      create(root) {
        if (!this.metadata) {
          this.metadata = {};
        }
        this.metadata.hidden = !this.visible;
        return {
          self: this,
          init: null,
          metadata: this.metadata
        };
      }
    }
  }
};
