function saveToBusinessComponentGroup(design, controlList) {
  let _this = design; //ddd
  dsf.layer.openDialog({
    title: "",
    fullscreen: false,
    content: "DesBusinessComponent",
    width: "500px",
    height: "500px",
    btns: [
      {
        text: "",
        handler: (res) => {
          let data = res.radioData;

          let sublist = _.map(controlList, (el, i) => {
            let metadataInfo = el.metadata;
            let metadataList = [];
            metadataInfo = dsf.type(metadataInfo) == "array" ? metadataInfo : [metadataInfo];
            _.each(metadataInfo, (m) => {
              m.metadata.id = "";
              m.metadata.at = "";
              metadataList.push(m.metadata);
            });
            return {
              "dsfa_rm_bc_list.version": "1",
              "dsfa_rm_bc_list.name": el.props.label,
              "dsfa_rm_bc_list.code": el.props.caption,
              "dsfa_rm_bc_list.md": metadataList ? JSON.stringify(metadataList) : "",
              "dsfa_rm_bc_list.content": JSON.stringify(el.props, (key, val) => {
                //
                if (key === "designId") {
                  return "";
                } else {
                  return val;
                }
              }),
              "dsfa_rm_bc_list.ds_order": i + 1
            };
          });
          let params = {
            namespace: "dsfa.rm.bc",
            pageName: "edit",
            data: JSON.stringify({
              dsfa_rm_bc_list: sublist,
              _id: data["dsfa_rm_id"],
              "dsfa_rm.ID": data["ID"],
              "dsfa_rm.dsfa_rm_id": data["dsfa_rm_id"]
            })
          };
          _this.loading = true;
          _this.$webAPI
            .formSave(params)
            .then(({ data }) => {
              if (data.state == "20000") {
                dsf.layer.pc.message("", true);
                _this.loadBusinessComponetGroup([{ id: data.data["dsfa_rm.dsfa_rm_id"] }]);
              } else {
                dsf.layer.pc.message(data.message, false);
              }
            })
            .catch((ex) => {
              dsf.httpError(ex, { message: "" });
              // dsf.layer.pc.message("", false);
            })
            .finally(() => {
              _this.loading = false;
            });
        }
      },
      {
        text: ""
      }
    ]
  });
}
function setMetaDataDict(metadastas) {
  let dict = {};
  if (!metadastas) {
    return dict;
  }
  for (let key in metadastas) {
    let arr = key.split(".");
    let prefix = arr[0];
    let name = arr[1] ? arr[1] : "";
    if (metadastas[key].level == 1 && !name) {
      dict[key] = {
        id: key,
        name: metadastas[key].name || "",
        type: "subtable",
        fieldsLength: 0,
        valueAttributesRemark: {},
        valueAttributes: [],
        level: 1
      };
    } else {
      let va = metadastas[key].valueAttributes;
      let valueAttributesRemark = _.map(va, (it) => {
        return { text: it.name, value: it.code }
      })
      dict[key] = {
        id: key,
        name: metadastas[key].name,
        type: "field",
        level: 0,
        hidden: metadastas[key].hidden,
        valueAttributesRemark: valueAttributesRemark,
        fieldsLength: metadastas[key].valueAttributes && metadastas[key].valueAttributes.length,
        valueAttributes: metadastas[key].valueAttributes && _.map(metadastas[key].valueAttributes, "code")
      };
      if (dict[prefix]) {
        dict[key].level = 1;
        dict[key].inSubTable = prefix;
      }
    }
  }
  return dict;
}
function isFormPage(type) {
  return postForm.indexOf(type) >= 0
}

