var isAuthenticated = false;

export default {
  type: "wechat",
  init() {
    let promist = new Promise((resolve, reject) => {
      var el = document.createElement('script');
      el.src = "https://res.wx.qq.com/open/js/jweixin-1.6.0.js"
      document.body.appendChild(el);
      el.onload = function () {
        resolve()
      }
      el.onerror = function () {
        reject();
      }
    });
    return promist.then(() => {
      //js
      return authentication();
    })
  },
  jsApiList: ["onMenuShareTimeline",
    "onMenuShareAppMessage",
    "onMenuShareQQ",
    "onMenuShareWeibo",
    "onMenuShareQZone",
    "startRecord",
    "stopRecord",
    "onVoiceRecordEnd",
    "playVoice",
    "pauseVoice",
    "stopVoice",
    "onVoicePlayEnd",
    "uploadVoice",
    "downloadVoice",
    "chooseImage",
    "previewImage",
    "uploadImage",
    "downloadImage",
    "translateVoice",
    "getNetworkType",
    "openLocation",
    "getLocation",
    "hideOptionMenu",
    "showOptionMenu",
    "hideMenuItems",
    "showMenuItems",
    "hideAllNonBaseMenuItem",
    "showAllNonBaseMenuItem",
    "closeWindow",
    "scanQRCode",
    "chooseWXPay",
    "openProductSpecificView",
    "addCard",
    "chooseCard",
    "openCard"
  ],
  methods: {
    authentication,
    camera: {
      //
      async scan(opt = {}) {
        opt = {
          needResult: 1, // 01
          scanType: ["qrCode", "barCode"], // 
          ...opt
        }
        if (!isAuthenticated) await authentication().catch(() => { })
        return new Promise((resolve, reject) => {
          if (!isAuthenticated) return reject('')
          wx.scanQRCode({
            needResult: opt.scanType, // 01
            scanType: opt.scanType, // 
            success: function (res) {
              resolve((res && res.resultStr) || '')
            },
            fail: function (err) {
              reject(err)
            },
            complete: function (val) { }
          })
        })
      }
    },
    //  api 
    deviceready(cb) {
      if (!dsf.type(cb) == 'function') return console.warn('deviceready ')
      document.addEventListener(
        "deviceready",
        () => {
          cb()
        },
        false
      );
    },
  }
}

//
function authentication() {
  let url = dsf.url.getWebPath('api/wechat/auth/js/token', $$webRoot.dsfa);//
  return new Promise((resolve, reject) => {
    if (isAuthenticated) resolve()
    dsf.http.get(url, { url: window.encodeURIComponent(window.location.href) })
      .done((res) => {
        if (res.data.jsApiTicket) {
          wx.config({
            debug: false, // ,apialertpclogpc
            appId: res.data.appId, // 
            timestamp: res.data.timestamp, // 
            nonceStr: res.data.nonceStr, // 
            signature: res.data.signature, // 
            jsApiList: ['scanQRCode'] // JS
          });
          wx.ready(function () {
            isAuthenticated = true;
            resolve(true)
          });
          wx.error(function (res) {
            isAuthenticated = false
            reject(res)
          });
        }
      })
      .error((res) => {
        isAuthenticated = false
        reject(res)
      })
  })
}