import formControl from "./formControl";
// import DsfQITitle from "_platform/pc/question/DsfQITitle";


export default {
  mixins: [formControl],
  props: {
    required: {
      type: Boolean,
      default: true,
    },
    // 
    isQuestionControl: {
      type: Boolean,
      default: true
    },
    // index
    hasQuestionIndex:{
      type: Boolean,
      default: true
    },
    // 
    questionIndex: {
      type: Number
    },
    // 
    questionBody: {
      type: String,
      default: ''
    },
    // 
    logicRela:{
      type: Object,
      default() {
        return {}
      }
    },
    // 
    logic:{
      type: Object,
      default() {
        return {}
      }
    }
  },
  computed:{
    hasRela:function () {
      return window._.isEmpty(this.logicRela)
    },
    showCtrl:function () {
      if(this.isDesign){
        return true
      }else{
        return this.visible
      }
    }
  },
  components:{ 
    // DsfQITitle 
  }
}