import control from "./control";

export default {
  mixins: [control],
  props:{
    //
    isTextContent:{
      type: Boolean,
      default: false
    },
    value: {
      type: [String, Number],
      default: ''
    },
    defaultValue: {
      type: [String, Number],
      default: ''
    },
    // 
    dataCapion: {
      type: String,
      default: ''
    },
    dataCapionType: {
      type: String,
      default: 'hoverPop'
    }
  },
  design: {
    //
    isMask: false
  },
  data () {
    return {
      $value: ''
    }
  },
  watch: {
    'value' (to) {
      if (!to && this.defaultValue) {
        // todo Express
        // let exp = dsf.Express;
        // this.$value = exp.replace(this.defaultValue.text);
        this.$value = this.defaultValue;
      }
    }
  },
  methods: {
    showHelper() {
      this.$openDialog({
        title: "",
        template: `<div v-html="params.value"></div>`,
        width: "30vw",
        height: "50vh",
        params: {
          value: this.dataCapion
        }
      });
    },
  }
};
