import helper from './helper';

let validatorMap = {
  'isNull': {
    name:"",
    validator(value) {
      let v = value;
      if (dsf.type(v) == 'string') {
        v = v.trim();
      }
      if (v === '' || v == "[]"|| v == "{}") {
        return true;
      } else if (dsf.type(v) == 'object' && dsf.isEmptyObject(v)) {
        return true;
      } else if (dsf.type(v) == 'array' && v.length == 0) {
        return true;
      } else if (dsf.isUnDef(v)) {
        return true;
      }
      return false;
    }
  },
  // 
  "isNumber": {
    name:"",
    validator(value, precision) {
      let reg = null;
      if (precision && precision > 0) {
        reg = new RegExp("^-?([0-9]\\d*|0(?!\\.0+$))(\\.\\d{0," + precision + "})?$", 'ig');
      } else {
        reg = new RegExp("^[+-]?(\\d|[1-9]\\d+)(\\.\\d+)?$", 'ig');
      }
      if (!reg.test(value)) {
        return false;
      }
      return true;
    }
  },
  // 
  "isNumberOver": {
    name:"",
    validator(value, ceil) {
      value = parseFloat(value);
      ceil = parseFloat(ceil);
      if (value > ceil) {
        return true;
      }
      return false;
    }
  },
  // 
  "isNumberUnder":{
    name:"",
    validator(value, floor) {
      value = parseFloat(value);
      floor = parseFloat(floor);
      if (value < floor) {
        return true;
      }
      return false;
    }
  },
  // 
  "precision":{
    name:"",
    validator(value, precision) {
      value = value.toString();
      const reg = new RegExp("^-?([0-9]\\d*|0(?!\\.0+$))(\\.\\d{0," + precision + "})?$", 'ig');
      if (!reg.test(value)) {
        return false;
      }
      return true;
    }
  },
  "maxLength":{
    name:"",
    validator(value,maxlength){
      if(value.length>maxlength){
        return true;
      }
      return false;
    }
  },
  "minLength":{
    name:"",
    validator(value,minLength){
      if(value.length<minLength){
        return true;
      }
      return false;
    }
  },
  // 
  "isDate": {
    name:"",
    validator(value, format = 'yyyy-mm-dd') {
      try {
        let d = dsf.date.parse(value, format);
        if (d == 'Invalid Date' || (d.getFullYear() + '').length > 4) {
          if (value.length != format.length) {
            return false;
          }
          ['y', 'M','m', 'D', 'd', 'H', 'h', 'i', 's'].forEach(v => {
            let i = format.indexOf(v);
            if (i > -1) {
              value = value.substring(0, i) + '-' + value.substr(i);
              format = format.substring(0, i) + '-' + format.substr(i);
            }
          });
          d = dsf.date.parse(value, format);
        }
        if (d == 'Invalid Date') {
          return false;
        }
        let value2 = dsf.date.format(d, format);
        return value == value2;
      } catch(e) {
        return false;
      }
    }
  },
  // 
  'isIDCard': {
    name:"",
    type:"express",
    validator(value) {
      //(15)
      const isIDCard1 = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$/;
      //(18)
      const isIDCard2 = /^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
      if (!isIDCard1.test(value) && !isIDCard2.test(value)) {
        return false;
      }
      return true;
    }
  },
  // ()
  'isIDCardFull': {
    name:"",
    type:"express",
    validator(value) {
      const res = dsf.idCode(value);
      return res.type;
    }
  },
  // 
  'isMobile': {
    name:"",
    type:"express",
    validator(value) {
      var reg = /^1(3|4|5|7|8|9|6)\d{9}$/;
      if (!reg.test(value)) {
        return false;
      }
      return true;
    }
  },
  // 
  "isTelPhone": {
    name:"",
    type:"express",
    validator(value) {
      var reg = /^(0\d{2,3}-)?\d{7,8}$/;
      if (!reg.test(value)) {
        return false;
      }
      return true;
    }
  },
  // 
  "isMobileOrTelPhone": {
    name:"/",
    type:"express",
    validator(value) {
      var mobile = /^1(3|4|5|7|8|9|6)\d{9}$/;
      var tel = /^(0\d{2}-)?\d{7,8}$/;
      if (!mobile.test(value) && !tel.test(value)) {
        return false;
      }
      return true;
    }
  },
  // 
  'isEmail': {
    name:"",
    type:"express",
    validator(value) {
      var reg = /\w+((-w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+/;
      if (!reg.test(value)) {
        return false;
      }
      return true;
    }
  },
  // 
  "isPlateNumber": {
    name:"",
    type:"express",
    validator(value) {
      var reg = /^(([][A-Z](([0-9]{5}[DF])|([DF]([A-HJ-NP-Z0-9])[0-9]{4})))|([][A-Z][A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9]))$/;
      if (!reg.test(value)) {
        return false;
      }
      return true;
    }
  },
  // 
  "isCombination": {
    name: "",
    type: "express",
    validator(value) {
      var number = /[0-9]+/;
      var ss = /[a-zA-Z]+/;
      if (!number.test(value) || !ss.test(value)) {
        return false;
      }
      return true;
    }
  },
  // 
  "isPostcode": {
    name: "",
    type: "express",
    validator(value) {
      var number = /^\d{6}$/;
      return number.test(value);
    }
  }
}

let validate = function() {
  let args = Array.prototype.slice.call(arguments, 0);
  let validator = args[0];
  let params = args.slice(1);
  return validatorMap[validator].validator.apply(this, params);
}

if(window.$$validate){
  helper.mix(validatorMap,window.$$validate)
}
validate.map = validatorMap;
export default validate;